.gallery {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  padding: 8em 2em 4em 2em;
  height: 100%;
  width: 100%;
  &__contents {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
  }
  &__empty {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
