@import './Subscription/style';
.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #eee;

  &__social {
    height: 60px;
    width: 60px;
    a {
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        &:hover {
          background: #eee;
        }
      }
    }
  }
}
